/* Gallery container */
.gallery-container {
   text-align: center;
   padding: 20px;
   background-color: #f9f9f9;
}

.gallery-container h2 {
   color: #d4a200;
   margin-bottom: 20px;
   font-size: 1.5rem; /* Adjusted for mobile */
}

/* Gallery media styles */
.gallery-media {
   display: flex;
   flex-wrap: wrap;
   gap: 10px;
   justify-content: center;
}

/* Video wrapper styling */
.video-wrapper {
   position: relative;
   width: calc(33.33% - 10px); /* Three videos per row */
   max-width: 150px;
   cursor: pointer;
   transition: transform 0.3s, box-shadow 0.3s;
}

.video-wrapper.playing {
   transform: scale(1.05);
   box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.2);
}

.video-wrapper:hover {
   transform: scale(1.02);
   box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.2);
}

/* Mobile responsiveness */
@media (max-width: 768px) {
   .video-wrapper {
      width: calc(50% - 10px); /* Two videos per row on mobile */
   }
}

@media (max-width: 480px) {
   .video-wrapper {
      width: 100%; /* One video per row on smaller screens */
   }
}

/* Video styles */
.gallery-video {
   width: 100%;
   height: auto;
   border-radius: 8px;
   object-fit: cover;
   background: #000;
}

.gallery-video:focus {
   outline: none;
}

/* Play overlay for video thumbnail */
.play-overlay {
   position: absolute;
   top: 50%;
   left: 50%;
   transform: translate(-50%, -50%);
   font-size: 2.5rem;
   color: rgba(255, 255, 255, 0.9);
   display: flex;
   align-items: center;
   justify-content: center;
   pointer-events: none;
   font-weight: bold;
   opacity: 1;
}

.video-wrapper.playing .play-overlay {
   display: none; /* Hide the overlay when the video is playing */
}
